.cont {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  gap: 10px;
  & > label {
    font-size: 18px;
    font-weight: 600;
  }
  & > input {
    padding: 6px 10px;
    border: 1px solid gray;
    border-radius: 4px;
  }
}

.container-div {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
