.ul-list {
  width: 100%;
  text-decoration: none;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 20px;

  padding-top: 10px;
  height: 100%;
  width: 100%;
  & > a {
    padding: 8px;
    font-size: 18px;
    display: flex;
    align-items: center;
    gap: 5px;
    text-decoration: none;
    color: inherit;
  }
}
