.all-product {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px;
  border: 1px solid gray;
  border-radius: 6px;
  & > img {
    width: 50px;
  }
}

.detail-div {
  padding: 0 10px;
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
}
